
html, body {
  font-family: "Inter", sans-serif;
  padding: 0;
  margin: 0;
}

.header-text-box {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header h1 {
  font-size: 8vw;
  font-weight: 900;
  background-color: #000;
  color: #fff;
  display: block;
  padding: 0.5em;
  white-space: nowrap;
}

.header h1:nth-child(2) {
  position: absolute;
  background-color: #fff;
  color: #000;
  -webkit-clip-path: inset(-1% -1% 50% -1%);
  clip-path: inset(-1% -1% 50% -1%);
}

.header p {
  font-size: 2vw;
  font-weight: 900;
  margin-top: 1em;
  text-align: center;
}
.header p span {
  display: block;
  transform: rotate(90deg);
  margin-top: 0.25em;
}

.header {
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}